@import url("https://fonts.googleapis.com/css2?family=Homenaje&display=swap");

body {
    background-color: rgba(210, 245, 255, 0.26);
    background-image: url('./images/corona3bg.png');
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}

.image{
    width: 370px;
    margin-top: 10px;
    margin-bottom: 20px;
}

p{
    font-family: 'Homenaje', sans-serif;
    font-size: 1rem;
    margin-bottom: -8px;
}


@media (max-width: 770px) {
    .container{
        margin: 0 10%;
    }
    .image{
        width: 100%;
    }
}